:root {
  --color-alice-blue: #f2f5f7;
  --color-alice-blue2: #f2f5f6;
  --color-alice-blue3: #f8fdff;
  --color-whisper: #ebebeb;
  --color-deep-sky-blue: #00b2ff;
  --color-deep-sky-blue2: #00b3ff;
  --color-pacific-blue: #019bdf;
  --color-cerulean: #026593;
  --color-black-pearl: #041827;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "";
  font-display: swap;
  src: url("../fonts/.woff2") format("woff2");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
}
@font-face {
  font-family: "PTMono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  max-width: 1220px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 320px) {
  .container {
    padding: 0 25px;
  }
}

.button {
  padding: 0;
  width: 160px;
  min-height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: none;
  outline: none;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  /* iOS Safari */
  -webkit-touch-callout: none;
  /* Safari */
  -webkit-user-select: none;
  /* Konqueror HTML */
  -khtml-user-select: none;
  /* Old versions of Firefox */
  -moz-user-select: none;
  /* Internet Explorer/Edge */
  -ms-user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  user-select: none;
}
.button--header {
  border-radius: 3px;
  border: 1px solid var(--color-alice-blue2);
  color: var(--color-alice-blue);
  background-color: var(--color-black-pearl);
}
.button:hover, .button:focus {
  color: var(--color-pacific-blue);
}
.button:active {
  border-color: var(--color-pacific-blue);
}
.button:disabled {
  color: var(--color-black-pearl);
  background-color: var(--color-alice-blue2);
}
.button:focus:not(:focus-visible) {
  box-shadow: none;
  outline: 0;
}
.button:focus-visible {
  outline: 2px solid var(--color-deep-sky-blue);
  outline-offset: -2px;
  z-index: 50;
}

.link {
  padding: 0;
  box-sizing: border-box;
  border: none;
  outline: none;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  /* iOS Safari */
  -webkit-touch-callout: none;
  /* Safari */
  -webkit-user-select: none;
  /* Konqueror HTML */
  -khtml-user-select: none;
  /* Old versions of Firefox */
  -moz-user-select: none;
  /* Internet Explorer/Edge */
  -ms-user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  user-select: none;
}
.link--header {
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: var(--color-alice-blue);
}
.link:hover, .link:focus {
  color: var(--color-pacific-blue);
}
.link:active {
  opacity: 0.8;
}
.link:disabled {
  color: var(--color-black-pearl);
  background-color: var(--color-alice-blue2);
}
.link:focus:not(:focus-visible) {
  box-shadow: none;
  outline: 0;
}
.link:focus-visible {
  outline: 1px solid var(--color-alice-blue);
  outline-offset: -2px;
  z-index: 50;
}

.header {
  padding: 30px 0 35px;
  margin-top: -3px;
  min-height: 120px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: linear-gradient(180deg, var(--color-black-pearl) 84.9%, rgba(4, 24, 39, 0) 100%);
  overflow: hidden;
}
@media (max-width: 1023px) {
  .header {
    min-height: 100px;
    margin-left: 0;
    padding: 20px 0 25px;
  }
}
@media (max-width: 374px) {
  .header {
    margin-top: 0;
    min-height: 70px;
    padding: 15px 0 20px;
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  background-color: var(--color-black-pearl);
}

.header__logo-link {
  /* iOS Safari */
  -webkit-touch-callout: none;
  /* Safari */
  -webkit-user-select: none;
  /* Konqueror HTML */
  -khtml-user-select: none;
  /* Old versions of Firefox */
  -moz-user-select: none;
  /* Internet Explorer/Edge */
  -ms-user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  user-select: none;
}

.header__image {
  width: 215px;
  height: 55px;
}
@media (max-width: 1023px) {
  .header__image {
    width: 100%;
  }
}
@media (max-width: 374px) {
  .header__image {
    width: 35px;
    height: 35px;
  }
}

.header__button {
  margin-top: -5px;
  margin-right: 60px;
}
@media (max-width: 1023px) {
  .header__button {
    margin-right: 0;
    display: none;
  }
}

.menu {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .menu {
    margin-left: 0;
    margin-right: 20px;
  }
}
@media (max-width: 374px) {
  .menu {
    margin-right: 10px;
  }
}

.menu__list {
  margin: 0;
  padding: 0;
  margin-top: -5px;
  width: 480px;
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 28px;
  /* iOS Safari */
  -webkit-touch-callout: none;
  /* Safari */
  -webkit-user-select: none;
  /* Konqueror HTML */
  -khtml-user-select: none;
  /* Old versions of Firefox */
  -moz-user-select: none;
  /* Internet Explorer/Edge */
  -ms-user-select: none;
  /* Non-prefixed version, currently supported by Chrome, Edge, Opera and Firefox */
  user-select: none;
}
@media (max-width: 1023px) {
  .menu__list {
    width: 100px;
    gap: 20px;
    margin-top: -14px;
  }
}
@media (max-width: 374px) {
  .menu__list {
    margin-top: -6px;
  }
}

.menu__icon-email,
.menu__icon-phone {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .menu__icon-email,
.menu__icon-phone {
    margin-right: 0;
  }
}

.menu__link {
  width: 225px;
  min-height: 20px;
  display: flex;
}
@media (max-width: 1023px) {
  .menu__link {
    margin-bottom: 0;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 1023px) {
  .menu__link-text {
    display: none;
  }
}

